import api from '@/api/member';
import {
	mapState
} from 'vuex'
import tuiButton from "@/components/extend/button/button"
import tuiListCell from "@/components/list-cell/list-cell"
export default {
	components: {
		tuiButton,
		tuiListCell
	},
	data() {
		return {
			ischooseAddr: false,
			addressList: []
		}
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'member_id']),
	onLoad: function(options) {
		if (options.ischooseAddr) {
			this.ischooseAddr = options.ischooseAddr;
		}
		this.getAddress();
	},
	created() {

	},
	onShow: function(e) {


		let pages = getCurrentPages();


		let currPage = pages[pages.length - 1];


		if (currPage.data.isDoRefresh == true) {


			currPage.data.isDoRefresh = false;


			this.getAddress();


		} else {


			//不用刷新


		}


	},
	methods: {
		//获取全部收货地址
		getAddress() {
			var param = {
				page: this.$pagination.page,
				limit: this.$pagination.limit
			}
			var url = api.getAddress + '/' + this.member_id;
			this.$postajax(url, param)
				.then(res => {
					console.log(JSON.stringify(res))
					if (res.code == 0) {
						if (res.data && res.data.length > 0) {
							this.addressList = res.data;
							this.addressList.forEach(item => {
								item.checked = false;
							});
						}


					}


				})
				.catch(err => {

				});
		},
		//删除地址
		deleteAddress(id) {
			uni.showModal({
				title: '删除',
				content: '您确定要删除收货地址吗?',
				showCancel: true,
				success: (res) => {
					if (res.confirm) {
						var url = api.deleteAddress + '/' + id;
						this.$postajax(url)
							.then(res => {
								if (res.code == 0) {
									this.tui.toast("删除成功", 2000, true);
									this.getAddress();
								}
							})
							.catch(err => {});
					}
				}
			});
		},
		//新增或地址
		editAddr(id) {
			uni.navigateTo({
				url: "../editAddress/editAddress?id=" + id
			})
		},
		//选择地址
		checkboxChange: function(item) {
			
			if(this.ischooseAddr){
				this.chooseAddr(item);
				/* console.log(JSON.stringify(item))
				var checked = item.id
				for (var i = 0; i < this.addressList.length; i++) {
					if (checked === this.addressList[i].id) {
						
						this.addressList[i].checked = true;
						this.$set(this.addressList, i, this.addressList[i])
					} else {
						
						this.addressList[i].checked = false;
						this.$set(this.addressList, i, this.addressList[i])
					}
				}
				 */
				
			}else{
				this.editAddr(item.id)
			}
			
		},
		//确认选择地址
		chooseAddr(item) {
			console.log(JSON.stringify(item))
			var pages = getCurrentPages();
			
			
			var currPage = pages[pages.length - 1]; //当前页面
			
			
			var prevPage = pages[pages.length - 2]; //上一个页面
			
			
			//直接调用上一个页面的setData()方法，把数据存到上一个页面中去
			
			
			prevPage.setData({
				isDoRefresh: true,
				addressId:item.id
			})
			
			
			uni.navigateBack();
			
			/* uni.navigateTo({
				url: "../submitOrder/submitOrder?addressId=" + item.id
			}) */
		}

		/* checkboxChange: function (e) {
		var items = this.addressList,
			values = e.detail.value;
		for (var i = 0, lenI = items.length; i < lenI; ++i) {
			const item = items[i]
			if(values.includes(item.value)){
				this.$set(item,'checked',true)
			}else{
				this.$set(item,'checked',false)
			}
		}
	} */
	}
}
